solid ' + (isValid ? '#A1A1A1;' : 'red;') : ''\" @input=\"onInput()\"\n                @focus=\"onFocus()\" @blur=\"onBlur()\"/>\n            <textarea v-if=\"inputDefinitions.type == 'textarea'\" class='textAreaClass'\n                :style=\"(inputChanged ? 'border: 1px solid ' + (isValid ? '#A1A1A1;' : 'red;') : '') + (inputDefinitions.textAreaHeight ? ' height:' + inputDefinitions.textAreaHeight : '')\"\n                :placeholder=\"inputDefinitions.placeholder\" v-model=\"inputValue\" @input=\"onInput()\"\n                :id=\"CryptoHelper.CreateGuid()\" @focus=\"onFocus()\" @blur=\"onBlur()\"></textarea>\n            <div v-if=\"visiblePopover && ValidationList.length > 0\"\n                :class=\"inputDefinitions.type == 'textarea' ? 'popover__content popover__content_textArea' : 'popover__content popover__content_input'\">\n                <div style=\"height:100%\" class='errorDiv'>\n                    <div v-for=\"item in ValidationList\" class='errorContent'>\n                        <i\n                            :class=\"item.isValid ? 'popoverIcon mdi mdi-check-circle' : 'popoverIcon mdi mdi-close-circle closeIcon'\"></i>\n                        <a class=\"popoverText\">{{ item.message }}</a>\n                    </div>\n\n                </div>\n            </div>\n        </div>\n    </div>\n\n\n\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue'\nimport { container } from '../../../../domain/core/diContainer';\nimport { IFormValidator, IFormValidatorOptions, IFormValidatorResponseItem } from '../../../../domain/infrastructure/IFormValidator';\nimport { CryptoHelper } from \"@stechquick/algae/lib/helpers/cryptoHelper\";\nimport { IIDEInputDefinitions } from '../../../../domain/presentation/IIDEInputDefinitions';\n\nconst emit = defineEmits([\"validateStatus\", \"getValue\"]);\n\ninterface IInputDefinitionsInternal extends IIDEInputDefinitions {\n    isRequired?: boolean;\n    infoText: string;\n    placeholder: string;\n    type: 'textarea' | 'text' | 'password' | 'mail';\n    textAreaHeight?: string;\n    value?: string;\n    validations?: IFormValidatorOptions;\n    disableRequiredIcon?: boolean;\n}\n\nconst formValidator = container.resolve<IFormValidator>(IFormValidator);\nconst ValidationList = ref<Array<IFormValidatorResponseItem>>([]);\nconst visiblePopover = ref(false);\nconst inputDefinitions = defineProps<IInputDefinitionsInternal>();\nconst inputValue = ref(inputDefinitions.value || \"\");\nconst isValid = ref<boolean>(!inputDefinitions.isRequired);\nconst inputChanged = ref<boolean>(false);\nvalidate();\n\nfunction onFocus() {\n    validate()\n}\nfunction onBlur() {\n    visiblePopover.value = false\n}\n\nfunction onInput(){\n    emit('getValue', inputValue.value);\n    inputChanged.value = true;\n    validate();\n}\n\nfunction validate() {\n    if(inputDefinitions.isRequired && inputValue.value.length == 0){\n        ValidationList.value = [];\n        setValidationResult(false);\n        return;\n    }\n\n    if ((!inputDefinitions.isRequired && inputValue.value.length == 0) || !inputDefinitions.validations) {\n        ValidationList.value = [];\n        setValidationResult(true);\n        return;\n    }\n    \n    ValidationList.value = formValidator.validate(inputValue.value, inputDefinitions.validations);\n    const isValidValue = ValidationList.value.filter(e => e.isValid == false).length == 0;\n    const validationResult = !(!isValidValue && inputValue.value.length > 0);\n    setValidationResult(validationResult);\n}\n\nfunction setValidationResult(result: boolean){\n        emit(\"validateStatus\", result);\n        visiblePopover.value = !result\n        isValid.value = result;\n}\n\n</script>\n\n<style scoped>\n.infoText {\n    color: #151233;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 14px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    opacity: 1;\n\n}\n\n.textAreaClass {\n    width: 100%;\n    outline: none;\n    height: 120px;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    resize: none;\n    margin-top: 3px;\n    padding-left: 7px;\n}\n\n.inputClass {\n    width: 100%;\n    outline: none;\n    height: 40px;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    margin-top: 3px;\n    padding-left: 7px;\n}\n\n.errorMessage {\n    color: #212121;\n    font-style: normal;\n    font-weight: 400;\n    font-family: 'ROBOTO';\n    font-size: 12px;\n    letter-spacing: 0px;\n    opacity: 1;\n}\n\n.validationErrorIcon {\n    color: red;\n    font-size: 13px;\n}\n\n\n::placeholder {\n    padding-left: 10px;\n    padding-top: 5px;\n    color: #A1A1A1;\n    font-style: normal;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    font-size: 14px;\n    opacity: 1px;\n}\n\n.requiredIcon {\n    color: #757575;\n    font-size: 15px;\n}\n\n.title {\n    background-color: red;\n}\n\n.dropdown-menu {\n    display: list-item;\n    position: absolute;\n    inset: 0px auto auto 0px;\n    margin: 0px;\n    background-color: #FFFFFF;\n    border: 0.5px solid var(--grey);\n    box-shadow: 2px 2px 4px #00000029;\n    border-radius: 2px;\n    padding: .5rem 0;\n}\n\n.deneme {\n    background-color: red;\n}\n\na {\n    text-decoration: none;\n}\n\n.popover__title {\n    font-size: 24px;\n    line-height: 36px;\n    text-decoration: none;\n    color: rgb(228, 68, 68);\n    text-align: center;\n    padding: 15px 0;\n}\n\n\n.popover__content {\n    opacity: 0;\n    visibility: hidden;\n    position: absolute;\n    left: 120px;\n    transform: translate(0, 10px);\n    background-color: white;\n    width: auto;\n    padding-left: 0px;\n    padding-right: 10px;\n    z-index: 10;\n    opacity: 1;\n    visibility: visible;\n    transform: translate(0, -20px);\n    transition: all 0.5s cubic-bezier(0.75, -0.02, 0.2, 0.97);\n    border: 0.5px solid grey;\n    background: #FFFFFF 0% 0% no-repeat padding-box;\n    box-shadow: 2px 2px 4px #00000029;\n    opacity: 1;\n    border-radius: 4px;\n\n}\n\n.popover__content_textArea {\n    margin-top: 17px\n}\n\n.popover__content_input {\n    margin-top: 24px\n}\n\n.popover__content:before {\n    position: absolute;\n    z-index: -1;\n    content: \"\";\n    right: calc(50% - 10px);\n    top: -8px;\n    border-style: solid;\n    background-color: white;\n    border-width: 0 10px 10px 10px;\n    border-color: transparent transparent transparent transparent;\n    transition-duration: 0.3s;\n    transition-property: transform;\n    border-left: 0.5px solid grey;\n    border-top: 0.5px solid grey;\n    transform: rotate(45deg);\n    margin-top: 2.5px;\n    /* border: 0.5px solid grey; */\n\n}\n\n\n.popoverText {\n    font-style: normal;\n    font-weight: 500;\n    font-size: 12px;\n    color: #212121;\n    letter-spacing: 0px;\n    opacity: 1;\n    margin-left: 5px;\n    padding-top: 3px;\n}\n\n.popoverIcon {\n    font-style: normal;\n    font-size: 16px;\n    letter-spacing: 0.29px;\n    color: #29C660;\n    opacity: 1;\n}\n\n.errorContent {\n\n    display: flex;\n}\n\n.closeIcon {\n    color: red;\n}\n\n.errorDiv {\n    margin-top: 10px;\n    margin-left: 10px;\n    margin-bottom: 12px;\n}\n</style>"],"sourceRoot":""}]);
